<template>
  <div class="dashboard-ecommerce">
    <div class="container-fluid dashboard-content ">
      <!-- BREADCRUMB -->
      <div class="row">
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
          <div class="page-header">
            <div class="d-flex justify-content-between">
              <h2 class="pageheader-title">Cores</h2>
            </div>
            <div class="page-breadcrumb">
              <nav aria-label="breadcrumb">
                <ol class="breadcrumb d-flex align-items-center">
                  <li class="breadcrumb-item">
                    <router-link to="/home" class="breadcrumb-link"
                      >Dashboard</router-link
                    >
                  </li>
                  <font-awesome-icon
                    icon="angle-right"
                    size="xs"
                    class="mx-1"
                  />
                  <li class="breadcrumb-item">
                    <router-link to="/cores" class="breadcrumb-link"
                      >Cores</router-link
                    >
                  </li>
                  <font-awesome-icon
                    icon="angle-right"
                    size="xs"
                    class="mx-1"
                  />
                  <li class="breadcrumb-item" aria-current="page">
                    <router-link :to="'/cores'" class="breadcrumb-link">{{
                      core.name
                    }}</router-link>
                  </li>
                  <font-awesome-icon
                    icon="angle-right"
                    size="xs"
                    class="mx-1"
                  />
                  <li class="breadcrumb-item active" aria-current="page">
                    Modifier
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>

      <Alert
        :type="errors.type"
        :message="errors.message"
        v-if="errors.visible"
        @dismiss="errors.visible = false"
      />

      <div class="row">
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
          <div class="card">
            <div
              class="card-header d-flex align-items-center justify-content-between"
            >
              <h5 class="mb-0">Modifier un core</h5>
            </div>
            <div class="card-body">
              <h4 class="mb-0">Informations générales</h4>
              <p>Les informations générales sur le core.</p>
              <form>
                <div class="row">
                  <div class="form-group col">
                    <label for="name">Nom du core</label>
                    <input
                      type="text"
                      class="form-control"
                      aria-describedby="nameHelp"
                      v-model="core.name"
                    />
                  </div>
                  <div class="form-group col">
                    <label for="name">Chemin du core</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="core.path"
                    />
                  </div>
                </div>
                <div class="row">
                  <div class="form-group col">
                    <label for="description">Bios (optionnel)</label>
                    <input
                      type="text"
                      class="form-control"
                      v-model="core.bios"
                    />
                  </div>
                </div>
              </form>
            </div>
            <div class="card-body text-center">
              <button class="btn btn-success" @click.prevent="edit">
                Enregistrer les modifications
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "EditCore",
  data: function() {
    return {
      core: {},
      errors: {
        visible: false,
        type: "danger",
        message: ""
      }
    };
  },
  mounted: function() {
    this.getCore();
  },
  methods: {
    getCore() {
      this.$http.get("core/" + this.$route.params.id + "/").then(resp => {
        this.core = resp.data;
      });
    },
    edit: function() {
      if (this.core) {
        let form = new FormData();
        form.append("name", this.core.name);
        form.append("path", this.core.path);
        form.append("bios", this.core.bios);
        this.$http
          .patch("core/" + this.$route.params.id + "/", form)
          .then(resp => {
            this.core = resp.data;
            this.$router.push("/cores");
          });
      }
    }
  }
};
</script>
