<template>
  <div class="dashboard-ecommerce">
    <div class="container-fluid dashboard-content ">
      <!-- BREADCRUMB -->
      <div class="row">
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
          <div class="page-header">
            <div class="d-flex justify-content-between">
              <h2 class="pageheader-title">Cores</h2>
            </div>
            <div class="page-breadcrumb">
              <nav aria-label="breadcrumb">
                <ol class="breadcrumb d-flex align-items-center">
                  <li class="breadcrumb-item">
                    <router-link to="/home" class="breadcrumb-link"
                      >Dashboard</router-link
                    >
                  </li>
                  <font-awesome-icon
                    icon="angle-right"
                    size="xs"
                    class="mx-1"
                  />
                  <li class="breadcrumb-item">
                    <router-link to="/cores" class="breadcrumb-link"
                      >Cores</router-link
                    >
                  </li>
                  <font-awesome-icon
                    icon="angle-right"
                    size="xs"
                    class="mx-1"
                  />
                  <li class="breadcrumb-item active" aria-current="page">
                    Ajouter un core
                  </li>
                </ol>
              </nav>
            </div>
          </div>
        </div>
      </div>

      <Alert
        :type="errors.type"
        :message="errors.message"
        v-if="errors.visible"
        @dismiss="errors.visible = false"
      />

      <div class="row">
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
          <div class="card">
            <div
              class="card-header d-flex align-items-center justify-content-between"
            >
              <h5 class="mb-0">Ajouter un core</h5>
            </div>
            <div class="card-body">
              <h4 class="mb-0">Informations générales</h4>
              <p>Les informations générales sur le nouveau core.</p>
              <form>
                <div class="row">
                  <div class="form-group col">
                    <label for="name">Nom du core</label>
                    <input
                      type="text"
                      class="form-control"
                      aria-describedby="nameHelp"
                      v-model="core.name"
                    />
                  </div>
                  <div class="form-group col">
                    <label for="name">Fichier Core</label>
                    <input
                      type="file"
                      class="form-control-file"
                      ref="core_file"
                    />
                  </div>
                </div>
                <div class="row">
                  <div class="form-group col">
                    <label for="bios">Fichier Bios (optionnel)</label>
                    <input
                      type="file"
                      class="form-control-file"
                      ref="core_bios"
                    />
                  </div>
                </div>
              </form>
            </div>
            <div class="card-body text-center">
              <button class="btn btn-success" @click.prevent="addCore">
                Enregistrer le core
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddCore",
  data: function() {
    return {
      core: {},
      errors: {
        visible: false,
        type: "danger",
        message: ""
      }
    };
  },
  mounted: function() {},
  methods: {
    addCore: function() {
      if (this.core) {
        let form = new FormData();
        form.append("name", this.core.name);
        form.append("path", this.$refs.core_file.files[0]);
        if (this.$refs.core_bios.files[0])
          form.append("bios", this.$refs.core_bios.files[0]);
        else
          form.append("bios", "");
        this.$http
          .post("core/", form, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(resp => {
            this.core = resp.data;
            this.$router.push("/cores");
          })
          .catch(() => {
            this.errors = {
              visible: true,
              type: "danger",
              message: "Impossible de créer un nouveau core."
            };
          });
      }
    }
  }
};
</script>
